import React, { Component, CSSProperties } from 'react'
import Footer from '../footer';
import Header from '../header';
// import NewsUpdate from './newsUpdate/newsUpdate';
import TotalAmountElem from './util/totalAmountElem';
import ActiveStakeSlider from './util/activeStakeSlider';
import BigNumber from 'bignumber.js'
import { Button, Card, Col, Container, Modal, ModalBody, ModalHeader, Row, UncontrolledTooltip } from 'reactstrap';

import stateStore from '../test/stateStore';
import { Profile, Reward, Stake } from './typings';
import { faWheat, faHandHoldingUsd, faNetworkWired, faExchange } from '@fortawesome/pro-duotone-svg-icons';
import EcoZystemCol from './util/ecoZystemCol';

interface Props {

}

export interface State {
  hasError: boolean,
  //profile
  profile: Profile,
  //token
  symbol?: string,
  // analysis: { balanceChange: number },
  // balance: TokenBalance,
  //stakes
  stakes: Array<Stake>,//active and inactive stakes 
  rewards: Array<Reward>
  showStakeModal: boolean
}

// const styled: CSSProperties = {
//   height: '80px', width: '80px', fontSize: '28px', fontWeight: 400
// }

class Dashboard extends Component<Props, State> {
  constructor(props: Props) {
    super(props)

    this.state = stateStore();

    if (this.state.profile.address) {
      this.state.profile.shortAddress = this.state.profile.address.substring(0, 6) + '...' + this.state.profile.address.substring(this.state.profile.address.length - 5, this.state.profile.address.length)
    }
  }

  // static defaultProps:Props = {
  //   // msg: 'Hello everyone!'
  // }

  // state = {}

  componentDidMount = () => {
    console.log('Dashboard mounted');
  }

  componentDidUpdate = () => {
    console.log('Dashboard did update');
  }

  componentWillUnmount = () => {
    console.log('Dashboard will unmount');
  }

  toggleStakeModal = () => {
    this.setState((prevState) => ({
      showStakeModal: !prevState.showStakeModal
    }))
  }

  bgColorCSSProp = (color: string) => {
    const styled: CSSProperties = {
      backgroundColor: color
    }
    return styled
  }

  // getters|modifiers
  calculateTotalAmount = (arr: Array<Stake>) => {
    return arr.map(val => new BigNumber(val.amount ?? "0")).reduce((a, b) => a.plus(b))
  }

  rewardsByType = (reward_type: number, days: number) => this.state.rewards
    .map(x => (reward_type === -1 ? true : x.sourceID === reward_type) && x.matureDate > new Date().setDate(new Date().getDate() - days) ?
      new BigNumber(x.amount) : new BigNumber("0")).reduce((a, b) => a.plus(b)).toFormat(3)

  totalStakesLength = (active: boolean) => this.state.stakes?.filter((stake) => stake.active === active).length

  // setters
  /* Test Only */
  updateBalance = (amount: number) => {
    this.setState((prevState) => ({
      profile: {
        ...prevState.profile,
        balance: new BigNumber(prevState.profile.balance).plus(amount).toString(),
      }
    }))
  }

  render() {
    if (this.state.hasError) {
      return <h1>Something went wrong.</h1>;
    }

    return (
      <div className="nk-wrap">
        <Header {...this.state.profile} />
        {/* content @s */}
        <div className="nk-content nk-content-lg nk-content-fluid">
          <Container fluid="xl" className="wide-lg">
            <div className="nk-content-inner">
              <div className="nk-content-body">
                <div className="nk-block-head">
                  <div className="nk-block-between-md g-3">
                    <div className="nk-block-head-content">
                      <div className="align-center flex-wrap pb-2 gx-4 gy-3">
                        <div>
                          <h4 className="nk-block-title"><span className="h6">Welcome</span> {this.state.profile.shortAddress}</h4>
                        </div>
                      </div>
                      <div className="nk-block-des">
                        <p>Here is a summary of your token assets.</p>
                      </div>
                    </div>
                    {this.state.stakes &&
                      <div className="nk-block-head-content d-none d-md-block">
                        <div className="nk-slider nk-slider-s1">
                          <div className="slider-init" data-slick='{"autoplay": true, "dots": true, "arrows": false, "fade": true}'>
                            {
                              this.state.stakes?.slice(0, 5).map(function (stake, i) {
                                if (stake.active) {
                                  return <ActiveStakeSlider key={i} {...stake} />;
                                }
                                return null;
                              })
                            }
                          </div>
                          <div className="slider-dots" />
                        </div>{/* .nk-slider */}
                      </div>
                    }
                  </div>{/* .nk-block-between */}
                </div>{/* .nk-block-head */}
                <div className="nk-block">
                  <Row className="gy-gs">
                    <Col md="6" lg="4">
                      <Card className="nk-wg-card is-dark card-bordered">
                        <TotalAmountElem amount={new BigNumber(this.state.profile.balance).toFormat(3)} change={0} title="Available Balance" />
                      </Card>{/* .card */}
                    </Col>
                    <Col md="6" lg="4">
                      <Card className="nk-wg-card is-s1 card-bordered">
                        <TotalAmountElem amount={this.calculateTotalAmount(this.state.stakes).toFormat(3)}/*  change={} */ title="Total Staked" />
                      </Card> {/* .card */}
                    </Col>
                    <Col md="12" lg="4">
                      <Card className="nk-wg-card is-s3 card-bordered">
                        <TotalAmountElem amount={this.calculateTotalAmount(this.state.rewards).toFormat(3)}/*  change={change} */ title="Total Rewards" />
                      </Card>{/* .card */}
                    </Col>
                  </Row>{/* .row */}
                </div>{/* .nk-block */}
                <div className="nk-block">
                  <Row className="gy-gs">
                    <Col md="6" lg="4">
                      <Card className="nk-wg-card card-bordered h-100">
                        <div className="card-inner h-100">
                          <div className="nk-iv-wg2">
                            <div className="nk-iv-wg2-title">
                              <h6 className="title">Your Balance</h6>
                            </div>
                            <div className="nk-iv-wg2-text">
                              <div className="nk-iv-wg2-amount ui-v2">
                                {this.state.profile.balance} eaZI
                                  {/* {this.state.analysis.balanceChange !== 0 &&
                                  <span className={this.state.analysis.balanceChange > 0 ? "change up" : "change down"}>
                                    <span className="sign" />{this.state.analysis.balanceChange}%</span>
                                } */}
                              </div>
                              <ul className="nk-iv-wg2-list">
                                <li>
                                  <span className="item-label">Staked Token</span>
                                  <span className="item-value">{new BigNumber(this.state.profile.stakedBalance).toFormat(3)}</span>
                                </li>
                                <li>
                                  <span className="item-label">Available Rewards</span>
                                  <span className="item-value">{new BigNumber(this.state.profile.balance).toFormat(3)}</span>
                                </li>
                                <li>
                                  <span className="item-label">Available Balance</span>
                                  <span className="item-value">{new BigNumber(this.state.profile.balance).toFormat(3)}</span>
                                </li>
                                <li className="total">
                                  <span className="item-label">Total Asset</span>
                                  <span className="item-value">{new BigNumber(this.state.profile.balance).plus(this.state.profile.stakedBalance).toFormat(3)}</span>
                                </li>
                              </ul>
                            </div>
                            <div className="nk-iv-wg2-cta">
                              <Button onClick={() => this.updateBalance(1)} color="primary" size="lg" block>Claim Rewards</Button>
                              <Button color="trans" block>Acquire Tokens</Button>
                            </div>
                          </div>
                        </div>
                      </Card>
                    </Col>
                    <Col md="6" lg="4">
                      <Card className="nk-wg-card card-bordered h-100">
                        <div className="card-inner h-100">
                          <div className="nk-iv-wg2">
                            <div className="nk-iv-wg2-title">
                              <h6 className="title">
                                Recent Rewards Earned
                                <em className="icon ni ni-info text-primary" id="recentRewardsTooltip" />
                                <UncontrolledTooltip target="recentRewardsTooltip">Rewards earned within the last 30 days</UncontrolledTooltip>
                              </h6>
                            </div>
                            <div className="nk-iv-wg2-text">
                              <div className="nk-iv-wg2-amount ui-v2">
                                {this.calculateTotalAmount(this.state.rewards).toFormat(3)} eaZI
                                  {/* {this.state.analysis.balanceChange !== 0 && <span className={this.state.analysis.balanceChange > 0 ? "change up" : "change down"}><span className="sign" />{this.state.analysis.balanceChange}%</span>} */}
                              </div>
                              <ul className="nk-iv-wg2-list">
                                <li>
                                  <span className="item-label">Governance rewards</span>
                                  <span className="item-value">{this.rewardsByType(0, 30)}</span>
                                </li>
                                <li>
                                  <span className="item-label">Yield rewards</span>
                                  <span className="item-value">{this.rewardsByType(1, 30)}</span>
                                </li>
                                <li>
                                  <span className="item-label">PAMM rewards</span>
                                  <span className="item-value">{this.rewardsByType(2, 30)}</span>
                                </li>
                                <li className="total">
                                  <span className="item-label">Total Rewards</span>
                                  <span className="item-value">{this.rewardsByType(-1, 30)}</span>
                                </li>
                              </ul>
                            </div>
                            <div className="nk-iv-wg2-cta">
                              <Button onClick={this.toggleStakeModal} color="primary" size="lg" block>eaZI ecosyZtem</Button>
                              <Button color="trans"/*  size="lg" */ block>Unstake Tokens</Button>
                            </div>
                          </div>
                        </div>
                      </Card>
                    </Col>{/* .col */}
                    <Col md="12" lg="4">
                      <Card className="nk-wg-card card-bordered h-100">
                        <div className="card-inner h-100">
                          <div className="nk-iv-wg2">
                            <div className="nk-iv-wg2-title">
                              <h6 className="title">
                                Recent Stakes
                                <em className="icon ni ni-info text-primary" id="recentStakesTooltip"></em>
                                <UncontrolledTooltip target="recentStakesTooltip">Short list of active stakes</UncontrolledTooltip>
                              </h6>
                            </div>
                            <div className="nk-iv-wg2-text">
                              <div className="nk-iv-wg2-amount ui-v2">{this.totalStakesLength(true)} Active</div>
                              <ul className="nk-iv-wg2-list">
                                {
                                  this.state.stakes.slice(0, 4).map(stake => (
                                    <li key={stake.date}>
                                      <span className="item-label"><a href="#!">{stake.title}</a> <small>- {stake.minInterest}% for {stake.matureAt}</small></span>
                                      <span className="item-value">{new BigNumber(stake.amount).toFormat(3)}</span>
                                    </li>
                                  ))
                                }
                              </ul>
                            </div>
                            <div className="nk-iv-wg2-cta">
                              <Button color="light" size="lg" block>See all Stakes</Button>
                              <div className="cta-extra">Analyze<button className="link link-dark">Expected Returns</button></div>
                            </div>
                          </div>
                        </div>
                      </Card>{/* .card */}
                    </Col>{/* .col */}
                  </Row>{/* .row */}
                </div>{/* .nk-block */}
              </div>
            </div>
          </Container>
        </div>
        {/* content @e */}
        {/* Modal Content Code */}
        <Modal className="stakeTokensModal" isOpen={this.state.showStakeModal} toggle={this.toggleStakeModal} size="xl" centered>
          <ModalHeader className="text-center">eaZI ecosyZtem</ModalHeader>
          <ModalBody className="modal-body-lg">
            <Row className="d-flex align-items-center justify-content-around">{/* card-group */}

              {/*  who get you rewarded from returns in  Stake your eaZI token in the biggest market-making pool strategies through Manual, Automated and Managed Percent Allocation Module*/}
              <EcoZystemCol icon={faNetworkWired} color="#228be6" title="eaZIDAM">{/* eaZIAMM, eaZIAM, eaZIPAMM, eaZITM, eaZITPool */}
                Delegate and grow your assets by staking in various pool of actively-trading strategies
              </EcoZystemCol>

              <EcoZystemCol icon={faHandHoldingUsd} color="#cc5de8" title="eaZILEND">
                Lend, borrow and earn interest on various inter-chain crypto assets
              </EcoZystemCol>

              <EcoZystemCol icon={faWheat} color="#82c91e" title="eaZIFARM">
                Stake your assets in select deFI pool to earn yields automatically
              </EcoZystemCol>

              <EcoZystemCol icon={faExchange} color="#22b8cf" title="ezChange">{/* #69e2dc */}
                Coming Soon!!!
              </EcoZystemCol>

            </Row>
          </ModalBody>
        </Modal>
        <Footer />
      </div>
    );
  }
}

export default Dashboard


