import React, { ReactNode } from 'react'
import { Col } from 'reactstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { IconDefinition } from '@fortawesome/pro-duotone-svg-icons'
import styled from 'styled-components';

interface Props {
    title: string,
    icon: IconDefinition,
    color: string,
    bgColor?: string,
    children: ReactNode//JSX.Element | string/*  | string[] */,
}

const ColStyle = styled(Col)`
&:hover {
    & .ez-col-description {
        display: block;
        text-align: center
    }
}
& .ez-col-description {
    display: none
}
`;

const EcoZystemCol = (props: Props) => {
    return (
        <ColStyle style={{ transition: 'background-color 0.2s linear' }} sm md lg className="text-center pb-4" /* style={this.bgColorCSSProp({props.bgColor})} */>
            <FontAwesomeIcon size="4x" icon={props.icon} className="ez-col-icon mb-4" color={props.color} />
            <h3 className="p-3 ez-col-title">{props.title}</h3>
            <h5 className="ez-col-description">{props.children}</h5>
        </ColStyle>
    )
}

export default EcoZystemCol
